<template id="home">

  <div class="mon-all" id="content" ref='content'>
<img src="../assets/img/monitoring/bg.png" alt="" class='bg'>
    <div class="topbar-mon clearfix">
      <div class="topbar-left">
        <div class="return" @click="backHome()">
          <img src="../assets/img/monitoring/out.png" alt="">
          <span>返回首页</span>
        </div>
        <!-- <div class="return" @click="setRuleFn()">
          <img src="../assets/img/monitoring/setting.png" alt="">
          <span>设定规则</span>
        </div> -->
        <div class="full" @click="screenfull">
          <img src="../assets/img/monitoring/full.png" alt="">
          <span>切换全屏</span>
        </div>
      </div>
      <div class="topbar-middle">
        <h2>{{hospitalName}}体温实时监测平台</h2>
        <h3 v-if='isShow==true'>{{depart}}</h3>
      </div>
      <div class="topbar-right">
        <p>{{date.year}}-{{date.month}}-{{date.strDate}} 星期{{date.week}}</p>
        <p>{{date.hour}}:{{date.minute}}:{{date.second}}</p>
      </div>
    </div>
    <div class="special" v-if='specialLists.length>0 && userInfo.role==2'>
      <div class="special-box"  ref='sli' :style=" { top:top } ">
        <div class="special-item" v-for="(item,index) in specialLists" :key="index">
          <div class="special-detail" >
            <i class='i-red' v-if="item.temperatureStatus=='RED'"></i>
            <i class='i-blue' v-if="item.temperatureStatus=='BLUE'"></i>
            <span class="cwh">{{item.roomName}}{{item.bedName}}</span>
            <span>{{item.patientName}}</span>
            <em class='em-red' v-if="item.temperatureStatus=='RED'">{{item.temperature}}℃</em>
            <em class='em-blue' v-if="item.temperatureStatus=='BLUE'">{{item.temperature}}℃</em>
          </div>
        </div>
      </div>
    </div>
    <div class="main-mon clearfix">
      <div class="main-container" v-if="isShow==true">
        <div class="item" v-for="(item,index) in bedList">
          <div class="item-box clearfix"  @mouseenter="enter(index)" @mouseleave="leave()"  v-if="item.recordState==0">
            <div class="item-top clearfix">
              <div class="cw cw-gray">{{item.bedName}}</div>
              <span class="name">{{item.patientName}}</span>
              <span class="num">{{item.caseNum}}</span>
            </div>
            <div class="item-bottom">
              <div class="wrapper wrapper-wrong">
                <div class="wave wave-wrong" style="height:50%"></div>
                <div class="red-span">预热中</div>
              </div>
              <em class='use'>已使用:0小时0分</em>
            </div>
            <div class="cover-item" v-if="isEnter== index">
              <div class="item-top clearfix">
                <div class="cw cw-gray">{{item.bedName}}</div>
                <span class="name">{{item.patientName}}</span>
                <span class="num">{{item.caseNum}}</span>
              </div>
              <div class="item-bottom cover-bottom">
                <div class="option-item" @click="toChangeMachine(item.roomName,item.bedName,item.patientName,item.machineNo,item.recordId)">
                  <img src="../assets/img/monitoring/change.png" alt="">
                  <span>更换设备</span>
                </div>
                <div class="option-item" @click='showHistory(item.recordId,item.useTimes)'>
                  <img src="../assets/img/monitoring/record.png" alt="">
                  <span>体温记录</span>
                </div>
                <div class="option-item" @click="toStopMachine(item.recordId,item.machineId)">
                  <img src="../assets/img/monitoring/stop.png" alt="">
                  <span>停止监测</span>
                </div>
              </div>
            </div>
          </div>
          <div class="item-box"  @mouseenter="enter(index)" @mouseleave="leave()" v-if="item.recordState==1">
            <div class="item-top clearfix">
              <div class="cw cw-high" v-if="item.temperatureStatus=='RED'">{{item.bedName}}</div>
              <div class="cw cw-low" v-if="item.temperatureStatus=='ORANGE'">{{item.bedName}}</div>
              <div class="cw" v-if="item.temperatureStatus=='GREEN'">{{item.bedName}}</div>
              <div class="cw cw-blue" v-if="item.temperatureStatus=='BLUE'">{{item.bedName}}</div>
              <span class="name">{{item.patientName}}</span>
              <span class="num">{{item.caseNum}}</span>
              <img src="../assets/img/monitoring/dc.png" class="dc" alt="" v-if="item.pattery<20">
            </div>
            <div class="item-bottom">
              <div class="wrapper wrapper-high" v-if="item.temperatureStatus=='RED'">
                <div class="wave wave-high" style="height:90%"></div>
                <span>{{item.temperature}}℃</span>
              </div>
              <div class="wrapper wrapper-low" v-if="item.temperatureStatus=='ORANGE'">
                <div class="wave wave-low" style="height:70%"></div>
                <span>{{item.temperature}}℃</span>
              </div>
              <div class="wrapper" v-if="item.temperatureStatus=='GREEN'">
                <div class="wave wave-normal" style="height:50%"></div>
                <span>{{item.temperature}}℃</span>
              </div>
              <div class="wrapper wrapper-bule" v-if="item.temperatureStatus=='BLUE'">
                <div class="wave wave-blue" style="height:30%"></div>
                <span>{{item.temperature}}℃</span>
              </div>
                <em class='use'>已使用:{{item.useTimes}}</em>
            </div>
            <div class="cover-item" v-if="isEnter== index">
              <div class="item-top clearfix">
                <!-- <div class="cw">{{item.bedName}}</div> -->
                <div class="cw cw-high" v-if="item.temperatureStatus=='RED'">{{item.bedName}}</div>
                <div class="cw cw-low" v-if="item.temperatureStatus=='ORANGE'">{{item.bedName}}</div>
                <div class="cw" v-if="item.temperatureStatus=='GREEN'">{{item.bedName}}</div>
                <div class="cw cw-blue" v-if="item.temperatureStatus=='BLUE'">{{item.bedName}}</div>
                <span class="name">{{item.patientName}}</span>
                <span class="num">{{item.caseNum}}</span>
              </div>
              <div class="item-bottom cover-bottom">
                <div class="option-item" @click="toChangeMachine(item.roomName,item.bedName,item.patientName,item.machineNo,item.recordId)">
                  <img src="../assets/img/monitoring/change.png" alt="">
                  <span>更换设备</span>
                </div>
                <div class="option-item" @click='showHistory(item.recordId,item.useTimes)'>
                  <img src="../assets/img/monitoring/record.png" alt="">
                  <span>体温记录</span>
                </div>
                <div class="option-item" @click="toStopMachine(item.recordId,item.machineId)">
                  <img src="../assets/img/monitoring/stop.png" alt="">
                  <span>停止监测</span>
                </div>
              </div>
            </div>
          </div>
          <div class="item-box"  @mouseenter="enter(index)" @mouseleave="leave()" v-if="item.recordState==2">
            <div class="item-top clearfix">
              <div class="cw cw-gray">{{item.bedName}}</div>
              <span class="name">{{item.patientName}}</span>
              <span class="num">{{item.caseNum}}</span>
              <img src="../assets/img/monitoring/dc.png" class="dc" alt="" v-if="item.pattery<20">
            </div>
            <div class="item-bottom">
              <div class="wrapper wrapper-wrong">
                <div class="wave wave-wrong" style="height:50%"></div>
                <div class="wrong-span">连接异常</div>
              </div>
                <em class='use'>已使用:{{item.useTimes}}</em>
            </div>
            <div class="cover-item" v-if="isEnter== index">
              <div class="item-top clearfix">
                <div class="cw cw-gray">{{item.bedName}}</div>
                <span class="name">{{item.patientName}}</span>
                <span class="num">{{item.caseNum}}</span>
              </div>
              <div class="item-bottom cover-bottom">
                <div class="option-item" @click="toChangeMachine(item.roomName,item.bedName,item.patientName,item.machineNo,item.recordId)">
                  <img src="../assets/img/monitoring/change.png" alt="">
                  <span>更换设备</span>
                </div>
                <div class="option-item" @click='showHistory(item.recordId,item.useTimes)'>
                  <img src="../assets/img/monitoring/record.png" alt="">
                  <span>体温记录</span>
                </div>
                <div class="option-item" @click="toStopMachine(item.recordId,item.machineId)">
                  <img src="../assets/img/monitoring/stop.png" alt="">
                  <span>停止监测</span>
                </div>
              </div>
            </div>
          </div>
          <div class="item-box clearfix"  v-if="item.recordState==3">
            <div class="item-top">
              <div class="cw cw-gray">{{item.bedName}}</div>
            </div>
            <p @click="toOpenMachine(item.bedId,index)">启用设备</p>
          </div>
          <div class="item-box"  @mouseenter="enter(index)" @mouseleave="leave()" v-if="item.recordState==4">
            <div class="item-top clearfix">
              <div class="cw cw-gray">{{item.bedName}}</div>
              <span class="name">{{item.patientName}}</span>
              <span class="num">{{item.caseNum}}</span>
              <img src="../assets/img/monitoring/dc.png" class="dc" alt="" v-if="item.pattery<20">
            </div>
            <div class="item-bottom">
              <div class="wrapper wrapper-wrong">
                <div class="wave wave-wrong" style="height:50%"></div>
                <div class="wrong-span">请确认是否佩戴</div>
              </div>
                <em class='use'>已使用:{{item.useTimes}}</em>
            </div>
            <div class="cover-item" v-if="isEnter== index">
              <div class="item-top clearfix">
                <div class="cw cw-gray">{{item.bedName}}</div>
                <span class="name">{{item.patientName}}</span>
                <span class="num">{{item.caseNum}}</span>
              </div>
              <div class="item-bottom cover-bottom">
                <div class="option-item" @click="toChangeMachine(item.roomName,item.bedName,item.patientName,item.machineNo,item.recordId)">
                  <img src="../assets/img/monitoring/change.png" alt="">
                  <span>更换设备</span>
                </div>
                <div class="option-item" @click='showHistory(item.recordId,item.useTimes)'>
                  <img src="../assets/img/monitoring/record.png" alt="">
                  <span>体温记录</span>
                </div>
                <div class="option-item" @click="toStopMachine(item.recordId,item.machineId)">
                  <img src="../assets/img/monitoring/stop.png" alt="">
                  <span>停止监测</span>
                </div>
              </div>
            </div>
          </div>
          <div class="item-box"  @mouseenter="enter(index)" @mouseleave="leave()" v-if="item.recordState==5">
            <div class="item-top clearfix">
              <div class="cw cw-blue">{{item.bedName}}</div>
              <span class="name">{{item.patientName}}</span>
              <span class="num">{{item.caseNum}}</span>
              <img src="../assets/img/monitoring/dc.png" class="dc" alt="" v-if="item.pattery<20">
            </div>
            <div class="item-bottom">
              <div class="wrapper wrapper-bule">
                <div class="wave wave-blue" style="height:30%"></div>
                <div class="wrong-span wrong5">未按规范粘贴</div>
              </div>
                <em class='use'>已使用:{{item.useTimes}}</em>
            </div>
            <div class="cover-item" v-if="isEnter== index">
              <div class="item-top clearfix">
                <div class="cw cw-blue">{{item.bedName}}</div>
                <span class="name">{{item.patientName}}</span>
                <span class="num">{{item.caseNum}}</span>
              </div>
              <div class="item-bottom cover-bottom">
                <div class="option-item" @click="toChangeMachine(item.roomName,item.bedName,item.patientName,item.machineNo,item.recordId)">
                  <img src="../assets/img/monitoring/change.png" alt="">
                  <span>更换设备</span>
                </div>
                <div class="option-item" @click='showHistory(item.recordId,item.useTimes)'>
                  <img src="../assets/img/monitoring/record.png" alt="">
                  <span>体温记录</span>
                </div>
                <div class="option-item" @click="toStopMachine(item.recordId,item.machineId)">
                  <img src="../assets/img/monitoring/stop.png" alt="">
                  <span>停止监测</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="choose-bed" v-if='isShow==true'>
        <div class="bed-btn" :class="allActive==1?'show-active':''" @click='chooseBedType(1)'>
          <img src="../assets/img/monitoring/all-icon.png" alt="">
          <span>全部显示</span>
        </div>
        <div class="bed-btn":class="allActive==0?'show-active':''" @click='chooseBedType(0)'>
          <img src="../assets/img/monitoring/some-icon.png" alt="">
          <span>部分显示</span>
        </div>
        <div class="input-box" v-if='allActive==0'>
          <Select class='select' filterable @on-change='onSelectChangeStart' clearable ref="resetSelectStart">
             <Option v-for="item in bedArrs" :value="item.bedId" :key="item.bedId">{{item.roomName}}{{item.bedName}}</Option>
           </Select>
           <span class='input-jg'>-</span>
           <Select class='select' filterable @on-change='onSelectChangeEnd' clearable ref="resetSelectEnd">
              <Option v-for="item in someList" :value="item.bedId" :key="item.bedId">{{item.roomName}}{{item.bedName}}</Option>
          </Select>
          <div class="change-bed-btn" @click='changeBed'>确定</div>
        </div>
        <div class="input-box" v-if='allActive==1'>
          <em class='no-edit'>{{startBed}}</em>
          <span class='input-jg'>-</span>
          <em class='no-edit'>{{endBed}}</em>
        </div>
      </div>
    </div>
    <div class="error-top" v-if='isShow==false'>
      <h6 class="error-tip">请使用科室管理员账号登陆</h6>
    </div>
    <Toast :flow="qysb">
      <div class="box qysb-box mon-box">
        <div class="container">
          <img src="../assets/img/monitoring/guanbi.png" class="close" alt="" @click="using()">
          <div class="title clearfix">
            <img src="../assets/img/monitoring/change1.png" alt="">
            <span>启用设备</span>
          </div>
          <div class="content qysb-content">
            <form  action="index.html" method="post" class="t-form">
              <div class="t-form-left">
                <div class="form-item clearfix">
                  <span class="sbbh form-name">设备编号：</span>
                  <Select class='select'  filterable @on-query-change="onQueryChange" @on-change='onSelectChange' clearable ref="resetSelect">
                     <Option v-for="item in machineList" :value="item.machineId" :key="item.machineId">{{ item.machineNo }}</Option>
                 </Select>
                </div>
                <div class="form-item clearfix">
                  <span class="form-name">患者姓名：</span>
                  <Input class="input" v-model="userName" placeholder="请输入患者姓名"/>
                </div>
                <div class="form-item clearfix">
                  <Input class="input zyh" v-model="caseNum" placeholder="请输入住院号"/>
                </div>
              </div>
              <div class="t-form-right">
                <img src="../assets/img/monitoring/case1.png" class='machineno-img' alt="">
                <a href="javascript:void(0)">导入信息</a>
              </div>
            </form>
          </div>
          <div class="ipt-group">
            <a href="javascript:void(0)" class="on" @click='beginMachine()' v-if='isBegin'>启用</a>
            <a href="javascript:void(0)" class="on" v-if='!isBegin'>启用</a>
            <a href="javascript:void(0)" class="off" @click="using()">取消</a>
          </div>
        </div>
      </div>
    </Toast>
    <Toast :flow="ghsb">
      <div class="box ghsb-box">
        <div class="container">
          <img src="../assets/img/monitoring/guanbi.png" class="close" alt="" @click="change()">
          <div class="title clearfix">
            <img src="../assets/img/monitoring/change1.png" alt="">
            <span>更换设备</span>
          </div>
          <div class="content ghsb-content">
            <form class="" action="index.html" method="post" class='t-form'>
              <div class="t-form-left">
                <div class="form-item form-left-item clearfix">
                  <span class="form-name">使用人：</span>
                  <em class="user-cw">{{changeroomName}}{{changebedName}}  {{changepatientName}}</em>
                </div>
                <div class="form-item form-left-item clearfix">
                  <span class="form-name">原设备编号：</span>
                  <span class="form-sb-num">{{changemachineNo}}</span>
                </div>
                <div class="form-item form-left-item clearfix">
                <span class="form-name">新设备编号：</span>
                <Select class='select' filterable @on-change='onSelectChange1' clearable ref="resetSelect1" >
                  <!-- v-model="changemachineId" -->
                   <Option v-for="item in machineList" :value="item.machineId" :key="item.machineId">{{ item.machineNo }}</Option>
               </Select>
              </div>
              </div>
              <div class="t-form-right">
                <img src="../assets/img/monitoring/case1.png" class='machineno-img' alt="">
                <a href="javascript:void(0)">导入信息</a>
              </div>
            </form>
          </div>
          <div class="ipt-group">
            <a href="javascript:void(0)" class="on" @click='changeMachine()'>更换</a>
            <a href="javascript:void(0)" class="off" @click="change()">取消</a>
          </div>
        </div>
      </div>
    </Toast>
    <Toast :flow="tzjc">
      <div class="box tzjc-box">
        <div class="container">
          <img src="../assets/img/monitoring/guanbi.png" class="close" alt="" @click="stop()">
          <div class="title clearfix">
            <img src="../assets/img/monitoring/stop1.png" alt="">
            <span>停止监测</span>
          </div>
          <div class="content tzjc-content">
            <div class="tzjc-box">
              <img src="../assets/img/monitoring/warn.png" alt="">
              <span>请确认是否停止温度监测服务？</span>
            </div>
          </div>
          <div class="ipt-group">
            <a href="javascript:void(0)" class="on" @click='stopMachine()'>确认</a>
            <a href="javascript:void(0)" class="off" @click="stop()">取消</a>
          </div>
        </div>
      </div>
    </Toast>
    <Toast :flow="lsjl">
      <div class="box history-box">
        <div class="container">
          <img src="../assets/img/monitoring/guanbi.png" class="close" alt="" @click="history()">
          <div class="title clearfix">
            <img src="../assets/img/tw.png" alt="">
            <span>体温记录</span>
          </div>
          <div class="content history-content">
            <div class="zx-pic" id='myChart' style="width:11.45rem;height:4.8rem;"></div>
          </div>
          <div class="ipt-group">
            <a href="javascript:void(0)" class="on" @click="history()">确认</a>
            <a href="javascript:void(0)" class="off" @click="history()">取消</a>
          </div>
        </div>
      </div>
    </Toast>
    <Modal
        v-model="modal1"
        title="注意："
        @on-ok="ok"
        @on-cancel="cancel">
        <p class='modal-tips'>系统中已存在住院号<span>{{wrong_caseNum}}</span>,对应姓名为<span>{{wrong_name}}</span>,请确认是否更新姓名？</p>
    </Modal>
