      getNowTime(){
        var date = new Date();
         var year = date.getFullYear();
         var month = date.getMonth() + 1;
         var strDate = date.getDate();
         var week = date.getDay();
         var hour =  date.getHours();
         var minute = date.getMinutes();
         var second = date.getSeconds();
         if (month >= 1 && month <= 9) {
           month = "0" + month;
         }
         if (strDate >= 0 && strDate <= 9) {
           strDate = "0" + strDate;
         }
         if (hour >= 0 && hour <= 9) {
           hour = "0" + hour;
         }
         if (minute >= 0 && minute <= 9) {
           minute = "0" + minute;
         }
         if (second >= 0 && second <= 9) {
           second = "0" + second;
         }
         switch(week) {
           case 0:
            week = "日";
            break;
        case 1:
            week = "一";
             break;
        case 2:
            week = "二";
             break;
        case 3:
            week = "三";
             break;
        case 4:
            week = "四";
             break;
        case 5:
            week = "五";
             break;
        case 6:
            week = "六";
      }
         that.date = {year:year,month:month,strDate:strDate,week:week,hour:hour,minute:minute,second:second}
