        })
      },
      /*启用设备*/
      beginMachine(machineId){
        console.log(that.machineId)
        var reg = /^[0-9a-zA-Z]*$/g;
        that.isBegin = !that.isBegin;
        setTimeout(function(){
          that.isBegin = !that.isBegin;
        },1000)
