                if(list.length==0){
                  if((item.temperatureStatus=='RED' || item.temperatureStatus=='BLUE')){
                    list.push(item);
                  }
                }else{
                  for(var i=0;i<list.length;i++){
                    if(item.bedId == list[i].bedId){
                      if(item.temperature != list[i].temperature){
                        if((item.temperatureStatus=='RED' || item.temperatureStatus=='BLUE')){
                          list[i]=item;
                        }else{
                          list.splice(i,1)
                        }
                      }
                    }
                  }
                }
              }
            })
            self.specialLists = list;
          }else if(response.code==300){
            self.error(response.message)
          }else{
            /*that.error('网络错误，请重试')*/
          }
        })
