                    yAxis: '37.5'
                },
                {
                    name: '低温',
                    lineStyle: {
                       color: '#007BB6'
                   },
                    yAxis: '36'
                },
              ]
            }
          }]
       });
      },
      enter(index){
        that.isEnter = index;
      },
      leave(){
        that.isEnter = -1;
      },
      toOpenMachine(bedId,index){
        that.using();
        that.bedId = bedId;
        that.searchMachine(0);
        // that.machineId = '';
        // let query = this.$refs[selectRef].$data.query;
        // if (query) {
        //   this.$refs[selectRef].$data.query = ''
        // }
      },
      toChangeMachine(changeroomName,changebedName,changepatientName,changemachineNo,changerecordId){
        that.changerecordId = changerecordId;
        that.changeroomName = changeroomName;
        that.changebedName = changebedName;
        that.changepatientName = changepatientName;
        that.changemachineNo = changemachineNo;
        that.change();
        that.searchMachine(0);
        /*that.changedmachineId = changedmachineId;*/
      },
      toStopMachine(stoprecordId,stopMachineId){
        that.stop();
        that.stoprecordId = stoprecordId;
        that.stopMachineId = stopMachineId;
      },
      using(){
        that.qysb = !that.qysb;
        if(that.qysb==false){
          that.caseNum ='';
          that.userName = '';
          this.$refs.resetSelect.clearSingleSelect();
        }
      },
      change(){
        that.ghsb = !that.ghsb;
        // if(that.ghsb==false){
        //   that.changemachineId = ''
        // }
        this.$refs.resetSelect1.clearSingleSelect();
      },
      stop(){
        that.tzjc = !that.tzjc;
      },
      showHistory(historyrecordId,historyUseTimes){
        that.historyrecordId = historyrecordId;
        that.historyUseTimes = historyUseTimes;
        // that.drawLine();
        that.getHistory();
        that.history();
      },
      history(){
        that.lsjl = !that.lsjl;
        if(that.lsjl==false){

        }
      },
      /*全屏模式*/
      // fullScreen () {
      //   let element = document.documentElement;
      //   if (this.fullscreen == true) {
      //       if (document.exitFullscreen) {
      //           document.exitFullscreen();
      //       } else if (document.webkitCancelFullScreen) {
      //           document.webkitCancelFullScreen();
      //       } else if (document.mozCancelFullScreen) {
      //           document.mozCancelFullScreen();
      //       } else if (document.msExitFullscreen) {
      //           document.msExitFullscreen();
      //       }
      //       this.fullscreen = false;
      //   } else {
      //       if (element.requestFullscreen) {
      //           element.requestFullscreen();
      //       } else if (element.webkitRequestFullScreen) {
      //           element.webkitRequestFullScreen();
      //       } else if (element.mozRequestFullScreen) {
      //           element.mozRequestFullScreen();
      //       } else if (element.msRequestFullscreen) {
      //           element.msRequestFullscreen();
      //       }
      //       this.fullscreen = true;
      //   }
      //   // this.fullscreen = !this.fullscreen;
      //   if(this.fullscreen==true){
      //     this.$refs.content.style.height = '100%'
      //   }else{
      //     this.$refs.content.style.height = 'auto'
      //   }
      //   storage.set('fullscreen',this.fullscreen);
      //   console.log(this.fullscreen)
      // },


      changes() {
         this.isFullscreen = screenfull.isFullscreen ;
         console.log(this.isFullscreen)
        },
       init() {
         console.log(screenfull.enabled)
         if (screenfull.enabled) {
         screenfull.on('change', this.changes)
         }
       },
      destroy() {
       if (screenfull.enabled) {
         screenfull.off('change', this.changes)
       }
      } ,
      screenfull() {
         if (!screenfull.enabled) {

           return false
         }
         screenfull.toggle();
         if(this.isFullscreen==true){
           this.$refs.content.style.height = '100%'
         }else{
           this.$refs.content.style.height = 'auto'
         }
         storage.set('fullscreen',this.isFullscreen);
         // this.isFullscreen = true;
       },
       /**
        * 是否全屏并按键ESC键的方法
        */
       checkFull() {
         var isFull = document.fullscreenEnabled || window.fullScreen || document.webkitIsFullScreen || document.msFullscreenEnabled;
         // to fix : false || undefined == undefined
         if (isFull === undefined) {
             isFull = false;
         }
         return isFull;
       }
    },
    components:{
        'Toast': Toast,
        swiper,
            swiperSlide
    }
  }

</script>
